import moment from "moment";
import {
  PricePoint,
  Hotel,
  PriceGridData,
  StayDates,
  BoundedRange,
  PricePointBounds as PricePointBound
} from "./types";
import { NumberRange } from "@blueprintjs/core";
import { DateRange } from "@blueprintjs/datetime";
import { useMemo, useState } from "react";

export const DEFAULT_HOTELS: string[] = [
  "The Ritz-Carlton Georgetown, Washington, D.C.",
  "The Westin Georgetown, Washington D.C.",
  "Washington Marriott Georgetown",
  "The Ritz-Carlton, Washington, DC",
  "The Mayflower Hotel, Autograph Collection",
  "The Westin Georgetown, Washington D.C."
];

export function formatDate(date: Date) {
  const m = moment(date);
  return {
    dayOfWeek: m.format("ddd"),
    date: m.format("M/D"),
    year: m.format("YYYY")
  };
}

export function dateToKey(date: Date) {
  const formatted = formatDate(date);
  return `${formatted.date}/${formatted.year}`;
}

export function datesToKey(stay: StayDates): string {
  return [stay.checkIn, stay.checkOut].map(dateToKey).join("-");
}

export function getLast<T>(array: T[]): T {
  return array[array.length - 1];
}

export function getStayLength(stay: StayDates): number {
  return moment(stay.checkOut).diff(moment(stay.checkIn), "days");
}

export function pricePointIsWithinStayLengthRange(
  stay: StayDates,
  stayLengthRange: NumberRange
) {
  const length = getStayLength(stay);
  return length >= stayLengthRange[0] && length <= stayLengthRange[1];
}

export function hotelIsWithinDistance(
  hotel: Hotel,
  distanceRange: NumberRange
) {
  return (
    hotel.distance >= distanceRange[0] && hotel.distance <= distanceRange[1]
  );
}

export function pricePointIsWithinPriceRange(
  price: number,
  range: NumberRange
) {
  return price >= range[0] && price <= range[1];
}

export function datesAreChronological(first?: Date, second?: Date): boolean {
  if (!first || !second) {
    return true;
  }
  return moment(second).diff(moment(first)) >= 0;
}

export function dateIsInRange(date: Date, range: DateRange) {
  return (
    datesAreChronological(range[0], date) &&
    datesAreChronological(date, range[1])
  );
}

export function getPriceFromGrid(
  grid: PriceGridData | undefined,
  stay: StayDates
) {
  if (grid) {
    const key = datesToKey(stay);
    const pricePoint = grid.map.get(key);
    return pricePoint && pricePoint.pricePerNight;
  }
}

export class PricePointMap<V extends PricePoint> extends Map<string, V[]> {
  public addPricePoint(key: string, pricePoint: V) {
    if (this.get(key) === undefined) {
      super.set(key, []);
    }
    const list = super.get(key);
    if (list === undefined) {
      throw Error(`No list for key: ${key} in PricePointMap`);
    }
    list.push(pricePoint);
    return this;
  }

  public set(): this {
    throw Error(
      "Calling set() in a PricePointMap is disallowed. Use addPricePoint instead."
    );
  }
}

export function getBucketCeiling(value: number) {
  if (value <= 1) {
    return 1;
  } else if (value <= 2) {
    return 2;
  } else if (value <= 5) {
    return 5;
  } else if (value <= 10) {
    return 10;
  } else {
    return Infinity;
  }
}

export function getBound<T>(
  range: BoundedRange<T>,
  bound: "max" | "min",
  defaultValue?: T
) {
  const setBound = range.bounds[bound === "min" ? 0 : 1];
  return setBound !== undefined ? setBound : defaultValue;
}

export function round(value: number, roundToNearest: number) {
  return Math.round(value / roundToNearest) * roundToNearest;
}

export function getLabelStepSizeForLabelCount(
  range: NumberRange,
  labelCount: number
) {
  const stepSize = (range[1] - range[0]) / labelCount;
  if (stepSize < 10) {
    return round(stepSize, 1);
  } else if (stepSize < 100) {
    return round(stepSize, 10);
  } else if (stepSize < 1000) {
    return round(stepSize, 100);
  } else {
    return round(stepSize, 1000);
  }
}

export function generateBookingLink(
  checkIn: Date,
  checkOut: Date,
  useCorporateCode: boolean,
  propertyCode?: string
) {
  const url = new URL(
    propertyCode
      ? "/reservation/availabilitySearch.mi"
      : "/search/submitSearch.mi",
    "https://www.marriott.com"
  );

  const params = new URLSearchParams();
  params.append("fromDate", moment(checkIn).format("MM/DD/YYYY"));
  params.append("toDate", moment(checkOut).format("MM/DD/YYYY"));
  params.append("numberOfRooms", "1");
  params.append("numberOfGuests", "1");
  params.append("currency", "USD");

  if (propertyCode) {
    params.append("propertyCode", propertyCode);
  } else {
    params.append(
      "destinationAddress.destination",
      "Georgetown,+Washington,+DC,+USA"
    );
  }

  if (useCorporateCode) {
    params.append("clusterCode", "corp");
    params.append("corporateCode", "P5R");
  }

  return `${url.toString()}?${params.toString()}`;
}

export function getAllDatesInRange(dates: string[], format: string): Date[] {
  if (dates.length === 0) {
    return [];
  }

  const momentDates = dates.map(d => moment(d, format).startOf("day"));
  let current = moment.min(momentDates);
  const max = moment.max(momentDates);

  const all = [];
  while (current <= max) {
    all.push(current.toDate());
    current.add(1, "days");
  }
  return all;
}

type AllBounds = [
  PricePointBound<Date | undefined>,
  PricePointBound<number>,
  PricePointBound<number>,
  PricePointBound<number>
];

interface BoundWithState<T> {
  bounds: [T, T];
  selection: [T, T];
  onChange: React.Dispatch<React.SetStateAction<[T, T]>>;
}
type AllBoundsWithState = [
  BoundWithState<Date | undefined>,
  BoundWithState<number>,
  BoundWithState<number>,
  BoundWithState<number>
];
export function useBounds(pricePoints: PricePoint[]): AllBoundsWithState {
  const bounds: AllBounds = useMemo(() => {
    const dateBound = new PricePointBound<Date | undefined>(
      [
        moment("2020-01-01", "YYYY-MM-DD").toDate(),
        moment("2000-01-01", "YYYY-MM-DD").toDate()
      ],
      p => moment(p.stayDates.checkIn).toDate(),
      p => moment(p.stayDates.checkOut).toDate()
    );

    const priceBound = new PricePointBound<number>(
      [Infinity, -1],
      p => p.pricePerNight
    );

    const stayLengthBound = new PricePointBound<number>([Infinity, -1], p =>
      getStayLength(p.stayDates)
    );

    const distanceBound = new PricePointBound<number>(
      [Infinity, -1],
      p => p.distance
    );

    const bounds: AllBounds = [
      dateBound,
      priceBound,
      stayLengthBound,
      distanceBound
    ];

    pricePoints.forEach(pricePoint => {
      bounds.forEach(bound => {
        bound.updateBounds(pricePoint);
      });
    });

    return bounds;
  }, [pricePoints]);

  function useRangeState<T>(bound: PricePointBound<T>): BoundWithState<T> {
    const [selection, onChange] = useState<[T, T]>(bound.bounds);
    return {
      bounds: bound.bounds,
      selection,
      onChange
    };
  }

  return [
    useRangeState(bounds[0]),
    useRangeState(bounds[1]),
    useRangeState(bounds[2]),
    useRangeState(bounds[3])
  ];
}
