import React, { useState } from "react";
import { NumberRange, RangeSlider, IRangeSliderProps } from "@blueprintjs/core";
import "./Slider.scss";
import classNames from "classnames";

interface Props extends IRangeSliderProps {
  value: NumberRange;
  labelPrefix?: string;
  labelSuffix?: {
    plural: string;
    singular: string;
  };
}

export const Slider: React.FC<Props> = (props: Props) => {
  const [localValue, setLocalValue] = useState<NumberRange>(props.value);
  const {
    className,
    value,
    labelPrefix,
    labelSuffix,
    ...spreadableProps
  } = props;
  return (
    <div>
      <RangeSlider
        {...spreadableProps}
        className={classNames("slider", className)}
        labelRenderer={label => (
          <span className="no-wrap">
            {(labelPrefix || "") +
              label.toLocaleString() +
              (labelSuffix
                ? label === 1
                  ? labelSuffix.singular
                  : labelSuffix.plural
                : "")}
          </span>
        )}
        value={localValue}
        onChange={onChange}
        onRelease={onRelease}
      />
    </div>
  );

  function onRelease(value: NumberRange) {
    setLocalValue(value);
    props.onRelease && props.onRelease(value);
  }

  function onChange(value: NumberRange) {
    setLocalValue(value);
    props.onChange && props.onChange(value);
  }
};
