export type Nullable<T> = T | null | undefined;

export function isPresent<T>(value: Nullable<T>): value is T {
  return value !== null && value !== undefined;
}

export interface ScrapedPricePoint {
  name: string;
  dates: [string, string];
  corporate_rate: boolean;
  price_per_night: number | undefined;
  sold_out: boolean;
  distance: number;
  booking_link: string;
}

export interface StayDates {
  checkIn: Date;
  checkOut: Date;
}

export interface PricePoint {
  hotelName: string;
  stayDates: StayDates;
  corporateRate: boolean;
  pricePerNight?: number | null;
  soldOut: boolean;
  distance: number;
  bookingLink?: string;
}

export interface PricePointWithPrice extends PricePoint {
  pricePerNight: number;
}

export interface RankedPricePrice extends PricePointWithPrice {
  rank: number;
}

export type PriceGridMap = Map<string, RankedPricePrice>;

export interface Hotel {
  name: string;
  distance: number;
  selected: boolean;
}

export interface PriceGridData {
  map: PriceGridMap;
  checkInDates: Date[];
  checkOutDates: Date[];
}

export interface Bucket<T> {
  floor: number;
  ceiling: number;
  items: T[];
}

export interface BoundedRange<T> {
  bounds: [T, T];
  onChange: (newRange: [T, T]) => void;
}
export interface ControlledBoundedRange<T> extends BoundedRange<T> {
  selection: [T, T];
}

type PricePointParser<T> = (pricePoint: PricePoint) => Nullable<T>;

export class PricePointBounds<T> {
  constructor(
    public bounds: [T, T],
    private readonly getMinCandidate: PricePointParser<T>,
    private readonly getMaxCandidate?: PricePointParser<T>
  ) {}

  public updateBounds(pricePoint: PricePoint) {
    const minCandidate = this.getMinCandidate(pricePoint);
    const maxCandidate = (this.getMaxCandidate || this.getMinCandidate)(
      pricePoint
    );
    let [min, max] = this.bounds;
    if (isPresent(minCandidate) && minCandidate < min) {
      min = minCandidate;
    }
    if (isPresent(maxCandidate) && maxCandidate > max) {
      max = maxCandidate;
    }
    this.bounds = [min, max];
  }
}
