import { List as ImmutableList } from "immutable";
import {
  PriceGridData,
  Hotel,
  PricePoint,
  PricePointWithPrice,
  StayDates
} from "./types";

export interface MatrixState {
  pricePoints: {
    byDates?: Map<string, PricePointWithPrice[]>;
    byHotel: Map<string, PricePoint[]>;
  };
  grid?: PriceGridData;
  hotels: ImmutableList<Hotel>;
  selectedSquare?: StayDates;
}

export interface LoadPricesAction {
  type: "LOAD_PRICES";
  grid: PriceGridData;
  pricePointsByDates: Map<string, PricePointWithPrice[]>;
}

export interface ToggleHotelSelection {
  type: "TOGGLE_HOTEL_SELECTION";
  hotel: Hotel;
}
export interface SelectSquareAction {
  type: "SELECT_SQUARE";
  square: StayDates;
}

export type Action =
  | LoadPricesAction
  | SelectSquareAction
  | ToggleHotelSelection;

export function matrixReducer(state: MatrixState, action: Action): MatrixState {
  switch (action.type) {
    case "LOAD_PRICES":
      return {
        ...state,
        grid: action.grid,
        pricePoints: {
          ...state.pricePoints,
          byDates: action.pricePointsByDates
        }
      };
    case "SELECT_SQUARE":
      return {
        ...state,
        selectedSquare: action.square
      };
    case "TOGGLE_HOTEL_SELECTION":
      return {
        ...state,
        hotels: state.hotels.map(hotel => ({
          ...hotel,
          selected:
            hotel.name === action.hotel.name ? !hotel.selected : hotel.selected
        }))
      };
  }
}
