import React, { Fragment, useState } from "react";
import { Hotel, Bucket, ControlledBoundedRange, BoundedRange } from "./types";
import { Slider } from "./Slider";
import { Tooltip, Checkbox, Collapse, Icon, Intent } from "@blueprintjs/core";
import { DateRangeInput } from "@blueprintjs/datetime";
import {
  hotelIsWithinDistance,
  getBound,
  getLabelStepSizeForLabelCount
} from "./utils";
import { Set as ImmutableSet } from "immutable";
import "./Filters.scss";
import { IconNames } from "@blueprintjs/icons";

interface Props {
  bucketedHotels: Bucket<Hotel>[];
  onChangeHotelSelection: (hotel: Hotel) => void;

  distanceRange: ControlledBoundedRange<number>;
  stayLengthRange: ControlledBoundedRange<number>;
  dateRange: BoundedRange<Date | undefined>;
  priceRange: ControlledBoundedRange<number>;
}

export const Filters: React.FC<Props> = (props: Props) => {
  const [expandedBuckets, setExpandedBuckets] = useState<ImmutableSet<number>>(
    ImmutableSet([props.bucketedHotels[0].ceiling])
  );

  return (
    <div className="filters">
      <h3>Filters</h3>
      <p>Distance from Georgetown</p>
      <Slider
        min={getBound(props.distanceRange, "min")}
        max={getBound(props.distanceRange, "max")}
        value={props.distanceRange.selection}
        onRelease={props.distanceRange.onChange}
        labelStepSize={getLabelStepSizeForLabelCount(
          props.distanceRange.bounds,
          6
        )}
        labelSuffix={{ plural: "mi", singular: "mi" }}
      />
      <p>Stay Length</p>
      <Slider
        value={props.stayLengthRange.selection}
        min={getBound(props.stayLengthRange, "min")}
        max={getBound(props.stayLengthRange, "max")}
        stepSize={1}
        labelStepSize={getLabelStepSizeForLabelCount(
          props.stayLengthRange.bounds,
          3
        )}
        labelSuffix={{ plural: " days", singular: " day" }}
        onRelease={props.stayLengthRange.onChange}
      />
      <p>Price Per Night</p>
      <Slider
        value={props.priceRange.selection}
        min={getBound(props.priceRange, "min")}
        max={getBound(props.priceRange, "max")}
        labelStepSize={getLabelStepSizeForLabelCount(
          props.priceRange.bounds,
          3
        )}
        stepSize={10}
        labelPrefix="$"
        onRelease={props.priceRange.onChange}
      />
      <div className="date-range-container">
        <DateRangeInput
          className="date-range"
          shortcuts={false}
          formatDate={date => date.toDateString()}
          parseDate={date => new Date(date)}
          minDate={props.dateRange.bounds[0]}
          maxDate={props.dateRange.bounds[1]}
          onChange={props.dateRange.onChange}
        />
      </div>
      <div className="hotels">
        {props.bucketedHotels.map(bucket => (
          <Fragment key={`${bucket.ceiling}`}>
            {renderTitle(bucket)}
            <Collapse isOpen={expandedBuckets.has(getKeyFromBucket(bucket))}>
              {bucket.items.map(hotel => {
                const outOfRange = !hotelIsWithinDistance(
                  hotel,
                  props.distanceRange.selection
                );
                return (
                  <div key={hotel.name}>
                    <Tooltip
                      content="Hotel is not in the selected distance range"
                      disabled={!outOfRange}
                    >
                      <Checkbox
                        checked={hotel.selected && !outOfRange}
                        disabled={outOfRange}
                        onChange={() => props.onChangeHotelSelection(hotel)}
                        key={hotel.name}
                        labelElement={
                          <span className="hotel-name">
                            {hotel.name}
                            <span className="distance">{hotel.distance}mi</span>
                          </span>
                        }
                      />
                    </Tooltip>
                  </div>
                );
              })}
            </Collapse>
          </Fragment>
        ))}
      </div>
    </div>
  );

  function renderTitle(bucket: Bucket<Hotel>) {
    const expanded = expandedBuckets.has(getKeyFromBucket(bucket));
    const anySelected = bucket.items.some(hotel => hotel.selected);
    return (
      <>
        <h4 className="clickable" onClick={toggleBucketExpansion(bucket)}>
          <Icon
            icon={expanded ? IconNames.CARET_DOWN : IconNames.CARET_RIGHT}
          />
          {renderBucketTitle(bucket)}
          {anySelected && (
            <Icon
              className="selected-icon"
              icon={IconNames.TICK}
              intent={Intent.SUCCESS}
            />
          )}
        </h4>
      </>
    );
  }

  function renderBucketTitle(bucket: Bucket<Hotel>) {
    const suffix = "miles";
    if (bucket.ceiling === Infinity) {
      return `${bucket.floor}+ ${suffix}`;
    }
    return `${bucket.floor} â€“ ${bucket.ceiling} ${suffix}`;
  }

  function toggleBucketExpansion(bucket: Bucket<Hotel>) {
    const key = getKeyFromBucket(bucket);
    return () => {
      setExpandedBuckets(
        expandedBuckets.has(key)
          ? expandedBuckets.remove(key)
          : expandedBuckets.add(key)
      );
    };
  }

  function getKeyFromBucket(bucket: Bucket<Hotel>) {
    return bucket.ceiling;
  }
};
