import React, { useMemo } from "react";
import { PricePointWithPrice, StayDates } from "./types";
import "./AvailableRooms.scss";
import {
  HTMLTable,
  Dialog,
  Classes,
  Popover,
  Button,
  Menu,
  MenuItem,
  Position
} from "@blueprintjs/core";
import moment from "moment";
import classNames from "classnames";
import { IconNames } from "@blueprintjs/icons";
import { generateBookingLink } from "./utils";

interface Props {
  visible: boolean;
  pricePoints?: PricePointWithPrice[];
  selectedSquare: StayDates;
  onClose: () => void;
}

interface HotelPrice {
  hotelName: string;
  cheapestRate?: number;
  corporateRate: boolean;
  bookingLink?: string;
}

function getRate(price: HotelPrice) {
  if (price.cheapestRate === null) {
    return "No Rate Available";
  }
  return `$${price.cheapestRate} (${
    price.corporateRate ? "Corporate Rate" : "Regular Rate"
  })`;
}

export const AvailableRooms: React.FC<Props> = (props: Props) => {
  const hotels = useMemo(() => {
    const byName = new Map<string, HotelPrice>();
    if (props.pricePoints) {
      props.pricePoints.forEach(pricePoint => {
        const currentBest = byName.get(pricePoint.hotelName);
        if (
          currentBest === undefined ||
          currentBest.cheapestRate === undefined ||
          (pricePoint.pricePerNight !== undefined &&
            pricePoint.pricePerNight < currentBest.cheapestRate)
        ) {
          byName.set(pricePoint.hotelName, {
            hotelName: pricePoint.hotelName,
            cheapestRate: pricePoint.pricePerNight,
            corporateRate: pricePoint.corporateRate,
            bookingLink: pricePoint.bookingLink
          });
        }
      });
    }
    return Array.from(byName.values()).sort((a, b) => {
      if (a.cheapestRate === null || a.cheapestRate === undefined) {
        return 1;
      } else if (b.cheapestRate === null || b.cheapestRate === undefined) {
        return -1;
      }
      return a.cheapestRate - b.cheapestRate;
    });
  }, [props]);

  return (
    <Dialog
      className={classNames("available-rooms", Classes.DARK)}
      isOpen={props.visible}
      title={renderTitle()}
      onClose={props.onClose}
    >
      <div className="body">
        <Popover content={renderBookMenu()} position={Position.RIGHT}>
          <Button
            text={"View all options on Marriott.com"}
            rightIcon={IconNames.CARET_RIGHT}
          />
        </Popover>
        <div className="table-container">
          <HTMLTable className="table" striped interactive>
            <tbody>
              {hotels.map(hotel => (
                <tr key={hotel.hotelName} onClick={openHotelBookingLink(hotel)}>
                  <td>{hotel.hotelName}</td>
                  <td>{getRate(hotel)}</td>
                </tr>
              ))}
            </tbody>
          </HTMLTable>
        </div>
      </div>
    </Dialog>
  );

  function renderTitle() {
    if (!props.selectedSquare) {
      return null;
    }
    const dates = [
      props.selectedSquare.checkIn,
      props.selectedSquare.checkOut
    ].map(d => moment(d).format("dddd, MMMM D, YYYY"));

    return (
      <>
        {dates[0]}
        <span className="space" />
        â€”
        <span className="space" />
        {dates[1]}
      </>
    );
  }

  function renderBookMenu() {
    return (
      <Menu>
        <MenuItem
          icon={IconNames.DOCUMENT_OPEN}
          text="Regular Rate"
          onClick={openSearch(false)}
        />
        <MenuItem
          icon={IconNames.DOCUMENT_OPEN}
          text="Corporate Rate"
          onClick={openSearch(true)}
        />
      </Menu>
    );
  }

  function openHotelBookingLink(hotel: HotelPrice) {
    return () => {
      if (hotel.bookingLink) {
        openLink(hotel.bookingLink);
      }
    };
  }

  function openSearch(useCorporateRate: boolean) {
    return () => {
      openLink(
        generateBookingLink(
          props.selectedSquare.checkIn,
          props.selectedSquare.checkOut,
          useCorporateRate
        )
      );
    };
  }

  function openLink(url: string) {
    window.open(url, "_blank");
  }
};
