import React from "react";
import { Text } from "@blueprintjs/core";
import classNames from "classnames";
import "./CheckOutRow.scss";
import { formatDate, dateToKey } from "./utils";

interface Props {
  checkInDates: Date[];
  checkOut?: Date;
  activeCheckIn?: Date;
  getPrice?: (checkOut: Date) => number | undefined;
  shouldTint?: (checkIn: Date) => boolean;
  onClick?: (checkIn: Date) => void;
  getPricePercentile?: (checkIn: Date) => number | undefined;
  onHover?: (checkIn: Date) => void;
  offHover?: (checkIn: Date) => void;
}

export const CheckOutRow: React.FC<Props> = (props: Props) => {
  return (
    <tr>
      {props.checkInDates.map(checkIn => {
        if (props.getPrice && props.checkOut) {
          const price = props.getPrice && props.getPrice(checkIn);
          return (
            <td
              key={dateToKey(checkIn)}
              onClick={onClickCell(checkIn)}
              onMouseEnter={() => props.onHover && props.onHover(checkIn)}
              onMouseLeave={() => props.offHover && props.offHover(checkIn)}
              className={classNames(
                {
                  clickable: price !== undefined,
                  active: props.activeCheckIn === checkIn,
                  tint: props.shouldTint && props.shouldTint(checkIn)
                },
                checkIn < props.checkOut ? getColorClass(checkIn) : "impossible"
              )}
            >
              {price !== undefined && (
                <Text className="price" ellipsize>{`$${price}`}</Text>
              )}
            </td>
          );
        } else {
          return (
            <th
              key={dateToKey(checkIn)}
              className={classNames(
                "checkin-date",
                props.shouldTint && props.shouldTint(checkIn) && "tint"
              )}
            >
              {renderDate(checkIn, true)}
            </th>
          );
        }
      })}
      {props.checkOut && (
        <th className="checkout-date">{renderDate(props.checkOut, false)}</th>
      )}
    </tr>
  );

  function renderDate(dateObj: Date, multiline: boolean) {
    const { dayOfWeek, date } = formatDate(dateObj);
    if (multiline) {
      return (
        <p>
          {dayOfWeek}
          <br />
          {date}
        </p>
      );
    } else {
      return (
        <p>
          {dayOfWeek}, {date}
        </p>
      );
    }
  }

  function onClickCell(checkIn: Date) {
    return () => props.onClick && props.onClick(checkIn);
  }

  function getColorClass(checkIn: Date) {
    if (!props.getPricePercentile) {
      return;
    }
    const percentile = props.getPricePercentile(checkIn);
    if (percentile === undefined) {
      return "no-price";
    }
    if (percentile >= 0.9) {
      return "cost-1";
    } else if (percentile > 0.7) {
      return "cost-2";
    } else if (percentile > 0.3) {
      return "cost-3";
    } else if (percentile > 0.1) {
      return "cost-4";
    } else {
      return "cost-5";
    }
  }
};
