import React, { useState } from "react";
import {
  Cell,
  Column,
  Table,
  TableLoadingOption,
  RenderMode,
  RowHeaderCell
} from "@blueprintjs/table";
import { PriceGridData, StayDates } from "./types";
import "./PriceGrid.scss";
import {
  formatDate,
  getPriceFromGrid,
  datesToKey,
  dateToKey,
  datesAreChronological
} from "./utils";
import { HTMLTable } from "@blueprintjs/core";
import { CheckOutRow } from "./CheckOutRow";
import { DateRange } from "@blueprintjs/datetime";

interface Props {
  grid?: PriceGridData;
  useBlueprintTable: boolean;
  onSelectSquare: (square: StayDates) => void;
  dateRange: DateRange;
}

const TINT_AFTER_CLICKED = false;

export const PriceGrid: React.FC<Props> = (props: Props) => {
  console.log(props.grid);
  const [selectedSquare, setSelectedSquare] = useState<StayDates | undefined>(
    undefined
  );
  const [hoveredSquare, setHoveredSquare] = useState<StayDates | undefined>(
    undefined
  );

  if (props.useBlueprintTable) {
    return (
      <Table
        defaultColumnWidth={85}
        enableRowResizing={false}
        enableColumnResizing={false}
        enableMultipleSelection={false}
        loadingOptions={
          !props.grid
            ? [
                TableLoadingOption.CELLS,
                TableLoadingOption.COLUMN_HEADERS,
                TableLoadingOption.ROW_HEADERS
              ]
            : []
        }
        renderMode={RenderMode.BATCH}
        rowHeaderCellRenderer={renderRowHeader}
        numRows={props.grid ? props.grid.checkInDates.length : 25}
        defaultRowHeight={25}
      >
        {renderColumns()}
      </Table>
    );
  } else {
    if (!props.grid) {
      return null;
    }
    return (
      <div className="grid">
        <div>
          <p className="checkin dates-label">Check in</p>
          <HTMLTable bordered condensed className="table-header">
            <thead>
              <CheckOutRow checkInDates={props.grid.checkInDates} />
            </thead>
          </HTMLTable>
          <div className="table-container">
            <HTMLTable bordered condensed className="html-table">
              <tbody>{props.grid.checkOutDates.map(renderRow)}</tbody>
            </HTMLTable>
          </div>
        </div>
        <p className="dates-label checkout-text">Check out</p>
      </div>
    );
  }

  function renderRow(checkOut: Date) {
    if (!props.grid) {
      return null;
    }
    return (
      <CheckOutRow
        key={dateToKey(checkOut)}
        checkOut={checkOut}
        checkInDates={props.grid.checkInDates}
        getPrice={getPrice(checkOut)}
        onClick={onSelectSquare(checkOut)}
        activeCheckIn={getActiveCheckIn(checkOut)}
        getPricePercentile={getPricePercentile(checkOut)}
        shouldTint={shouldTintSquare(checkOut)}
        onHover={onHoverSquare(checkOut)}
        offHover={onUnhoverSquare()}
      />
    );
  }

  function renderRowHeader(rowIndex: number) {
    if (props.grid) {
      return (
        <RowHeaderCell className="row-header">
          {formatDate(props.grid.checkInDates[rowIndex]).date}
        </RowHeaderCell>
      );
    }
    return <RowHeaderCell>{rowIndex}</RowHeaderCell>;
  }

  function renderColumns() {
    if (!props.grid) {
      return <Column />;
    }
    return props.grid.checkOutDates.map(checkOut => {
      return (
        <Column
          key={dateToKey(checkOut)}
          name={formatDate(checkOut).date}
          cellRenderer={cellRenderer(checkOut)}
        />
      );
    });
  }

  function cellRenderer(checkOut: Date) {
    return (rowIndex: number) => {
      let priceStr = "";
      if (props.grid) {
        const checkIn = props.grid.checkInDates[rowIndex];
        const price = getPriceFromGrid(props.grid, { checkIn, checkOut });
        if (price !== undefined) {
          priceStr = `$${price.toLocaleString()}`;
        }
      }
      return <Cell>{priceStr}</Cell>;
    };
  }

  function getPrice(checkOut: Date) {
    return (checkIn: Date) => {
      return getPriceFromGrid(props.grid, { checkIn, checkOut });
    };
  }

  function onSelectSquare(checkOut: Date) {
    return (checkIn: Date) => {
      if (getPriceFromGrid(props.grid, { checkIn, checkOut }) === undefined) {
        return;
      }
      setSelectedSquare({ checkIn, checkOut });
      props.onSelectSquare && props.onSelectSquare({ checkIn, checkOut });
    };
  }

  function onHoverSquare(checkOut: Date) {
    return (checkIn: Date) => setHoveredSquare({ checkIn, checkOut });
  }

  function onUnhoverSquare() {
    return () => setHoveredSquare(undefined);
  }

  function stayOverlaps(selected: StayDates, overlapper: StayDates) {
    if (selected.checkIn === overlapper.checkIn) {
      return datesAreChronological(selected.checkOut, overlapper.checkOut);
    }
    if (selected.checkOut === overlapper.checkOut) {
      return datesAreChronological(selected.checkIn, overlapper.checkIn);
    }
    return false;
  }

  function shouldTintSquare(checkOut: Date) {
    return (checkIn: Date): boolean => {
      const overlapsSelected =
        selectedSquare && stayOverlaps(selectedSquare, { checkIn, checkOut });
      const overlapsHovered =
        hoveredSquare && stayOverlaps(hoveredSquare, { checkIn, checkOut });

      return overlapsHovered || (TINT_AFTER_CLICKED && overlapsSelected);
    };
  }

  function getActiveCheckIn(checkOut: Date) {
    if (!TINT_AFTER_CLICKED) {
      return;
    }
    if (!selectedSquare) {
      return;
    }
    if (checkOut === selectedSquare.checkOut) {
      return selectedSquare.checkIn;
    }
  }

  function getPricePercentile(checkOut: Date) {
    return (checkIn: Date): number | undefined => {
      if (!props.grid) {
        return;
      }
      const thisSquare = props.grid.map.get(datesToKey({ checkIn, checkOut }));
      if (thisSquare !== undefined) {
        return (thisSquare.rank - 1) / (props.grid.map.size - 1);
      }
    };
  }
};
