import { Classes } from "@blueprintjs/core";
import { List as ImmutableList } from "immutable";
import React, {
  useCallback,
  useEffect,
  useMemo,
  useReducer,
  useState
} from "react";
import { AvailableRooms } from "./AvailableRooms";
import { PriceGrid } from "./PriceGrid";
import "./MarriottMatrix.scss";
import { matrixReducer, MatrixState } from "./matrixReducer";
import {
  Bucket,
  Hotel,
  PriceGridMap,
  PricePoint,
  PricePointWithPrice,
  StayDates
} from "./types";
import {
  datesToKey,
  DEFAULT_HOTELS,
  getLast,
  hotelIsWithinDistance,
  dateIsInRange,
  PricePointMap,
  pricePointIsWithinStayLengthRange,
  getBucketCeiling,
  pricePointIsWithinPriceRange,
  useBounds,
  getAllDatesInRange
} from "./utils";
import { Filters } from "./Filters";
import classNames from "classnames";
import moment from "moment";

interface Props {
  pricePoints: PricePoint[];
}

function createInitialState(props: Props): MatrixState {
  const hotelToPricePoints = new PricePointMap();
  const selectedHotelNames = new Set<string>();
  const hotels: Hotel[] = [];
  props.pricePoints.forEach(pricePoint => {
    const name = pricePoint.hotelName;

    hotelToPricePoints.addPricePoint(name, pricePoint);

    if (!selectedHotelNames.has(name)) {
      selectedHotelNames.add(name);
      hotels.push({
        name: name,
        distance: pricePoint.distance,
        selected: DEFAULT_HOTELS.includes(name)
      });
    }
  });

  hotels.sort((a, b) => a.distance - b.distance);

  return {
    pricePoints: {
      byHotel: hotelToPricePoints
    },
    hotels: ImmutableList(hotels)
  };
}

export const MarriottMatrix: React.FC<Props> = (props: Props) => {
  const [viewingAvailableRooms, setViewingAvailableRooms] = useState(false);

  const [state, dispatch] = useReducer(
    matrixReducer,
    createInitialState(props)
  );

  const [dateBounds, priceBounds, stayLengthBounds, distanceBounds] = useBounds(
    props.pricePoints
  );

  const [bucketedHotels, nameToHotel, anyHotelsSelected] = useMemo(() => {
    const hotels = state.hotels;
    const buckets: Bucket<Hotel>[] = [];
    const nameToHotel = new Map<string, Hotel>();
    let anyHotelsSelected = false;

    hotels.forEach(hotel => {
      nameToHotel.set(hotel.name, hotel);
      anyHotelsSelected =
        anyHotelsSelected ||
        (hotel.selected &&
          hotelIsWithinDistance(hotel, distanceBounds.selection));
      if (
        buckets.length === 0 ||
        hotel.distance > buckets[buckets.length - 1].ceiling
      ) {
        buckets.push({
          floor: buckets.length === 0 ? 0 : buckets[buckets.length - 1].ceiling,
          ceiling: getBucketCeiling(hotel.distance),
          items: [hotel]
        });
      } else {
        getLast(buckets).items.push(hotel);
      }
    });

    return [buckets, nameToHotel, anyHotelsSelected];
  }, [state.hotels, distanceBounds.selection]);

  const onSelectSquare = useCallback(
    (square: StayDates) => {
      setViewingAvailableRooms(true);
      dispatch({
        type: "SELECT_SQUARE",
        square
      });
    },
    [setViewingAvailableRooms, dispatch]
  );

  useEffect(() => {
    const datesToPricePoints = new PricePointMap<PricePointWithPrice>();
    const priceGridMap = new Map<string, PricePointWithPrice>();

    const dateFormat = "YYYY-MM-DD";
    const checkInDates = new Set<string>();
    const checkOutDates = new Set<string>();

    function maybeAddPricePointToGrid(pricePoint: PricePoint) {
      const {
        hotelName,
        pricePerNight,
        soldOut,
        stayDates: { checkIn, checkOut }
      } = pricePoint;

      checkInDates.add(moment(checkIn).format(dateFormat));
      checkOutDates.add(moment(checkOut).format(dateFormat));

      for (const date of [checkIn, checkOut]) {
        if (!dateIsInRange(date, dateBounds.selection)) {
          return;
        }
      }

      if (pricePerNight === null || pricePerNight === undefined || soldOut) {
        return;
      }

      const hotel = nameToHotel.get(hotelName);
      if (
        !hotel ||
        (anyHotelsSelected && !hotel.selected) ||
        !hotelIsWithinDistance(hotel, distanceBounds.selection) ||
        !pricePointIsWithinStayLengthRange(
          pricePoint.stayDates,
          stayLengthBounds.selection
        ) ||
        !pricePointIsWithinPriceRange(pricePerNight, priceBounds.selection)
      ) {
        return;
      }

      const pricePointWithPrice = {
        ...pricePoint,
        pricePerNight
      };
      const datesString = datesToKey({ checkIn, checkOut });
      const bestPriceForStay = priceGridMap.get(datesString);
      if (!bestPriceForStay || pricePerNight < bestPriceForStay.pricePerNight) {
        priceGridMap.set(datesString, pricePointWithPrice);
      }
      datesToPricePoints.addPricePoint(datesString, pricePointWithPrice);
    }

    if (anyHotelsSelected) {
      for (const [
        hotelName,
        pricePoints
      ] of state.pricePoints.byHotel.entries()) {
        const hotelForPricePoint = nameToHotel.get(hotelName);
        if (
          hotelForPricePoint &&
          hotelForPricePoint.selected &&
          hotelIsWithinDistance(hotelForPricePoint, distanceBounds.selection)
        ) {
          pricePoints.forEach(pricePoint => {
            if (
              pricePointIsWithinStayLengthRange(
                pricePoint.stayDates,
                stayLengthBounds.selection
              )
            ) {
              maybeAddPricePointToGrid(pricePoint);
            }
          });
        }
      }
    } else {
      props.pricePoints.forEach(maybeAddPricePointToGrid);
    }

    // add ranks
    const sortedPrices = [...priceGridMap.entries()].sort(
      ([_, a], [__, b]) => a.pricePerNight - b.pricePerNight
    );
    const mapWithRanks: PriceGridMap = new Map();
    sortedPrices.forEach(([dateString, _], i) => {
      const pricePoint = priceGridMap.get(dateString);
      if (pricePoint) {
        mapWithRanks.set(dateString, {
          ...pricePoint,
          rank: i + 1
        });
      }
    });

    dispatch({
      type: "LOAD_PRICES",
      pricePointsByDates: datesToPricePoints,
      grid: {
        map: mapWithRanks,
        checkInDates: getAllDatesInRange([...checkInDates], dateFormat),
        checkOutDates: getAllDatesInRange([...checkOutDates], dateFormat)
      }
    });
  }, [
    props.pricePoints,
    state.hotels,
    dateBounds.selection,
    distanceBounds.selection,
    stayLengthBounds.selection,
    priceBounds.selection,
    anyHotelsSelected,
    nameToHotel,
    state.pricePoints.byHotel
  ]);

  return (
    <div className={classNames("container", Classes.DARK)}>
      <div className="sidebar">{renderFilters()}</div>
      <div className="content">
        <h2>Marriott Matrix</h2>
        {renderTable()}
        {state.selectedSquare && (
          <AvailableRooms
            selectedSquare={state.selectedSquare}
            pricePoints={getPricePointsForStay(state.selectedSquare)}
            visible={viewingAvailableRooms}
            onClose={() => setViewingAvailableRooms(false)}
          />
        )}
      </div>
    </div>
  );

  function renderTable() {
    if (!state.grid) {
      return null;
    }
    return (
      <PriceGrid
        grid={state.grid}
        dateRange={dateBounds.selection}
        useBlueprintTable={false}
        onSelectSquare={onSelectSquare}
      />
    );
  }

  function renderFilters() {
    if (!state.grid) {
      return null;
    }
    return (
      <Filters
        dateRange={{
          bounds: dateBounds.bounds,
          onChange: dateBounds.onChange
        }}
        distanceRange={distanceBounds}
        stayLengthRange={stayLengthBounds}
        priceRange={priceBounds}
        bucketedHotels={bucketedHotels}
        onChangeHotelSelection={hotel => {
          dispatch({
            type: "TOGGLE_HOTEL_SELECTION",
            hotel
          });
        }}
      />
    );
  }

  function getPricePointsForStay(
    stay?: StayDates
  ): PricePointWithPrice[] | undefined {
    if (!state.grid || !stay || !state.pricePoints.byDates) {
      return [];
    }
    return state.pricePoints.byDates.get(datesToKey(stay));
  }
};
